import Chart from '@/Chart';
import React from 'react';



export const narrative_spec2cards = function(narrative_spec){
    let spec = {
        "chart" : narrative_spec.chart,
        "data": narrative_spec.data,
        "actions": []
    }
    
    let actions = []
    let idx_titleaction = []
    let step_actions = []
    if(narrative_spec.actions){
        actions = _.cloneDeep(narrative_spec.actions)
        actions.map((item, index) => {
            if(item.add === "title" && item.style.duration !== -1)
            idx_titleaction.push(index)
        })
        // duration delay = 0
        idx_titleaction.map((d) => {
            let eachaction = actions.slice(0,d+1)
            eachaction.map((item) => {
                if(item.style){
                    item.style.delay = 0
                    item.style.duration = 0
                }

            })
            // remove extra title
            eachaction.map((item, index) => {
                if (item.add === "title" && index !== eachaction.length-1){
                    eachaction.splice(index,1)
                }
            })
            let step_spec = _.cloneDeep(spec)
            step_spec.actions = eachaction
            step_actions.push(step_spec)
        })

    }
    return step_actions
}

export const narrativespec2Vis = function(spec, uuid) {

    return <Chart spec = {spec} uuid = { uuid }/>
}